<template>
  <div class="settings">
    <div class="header">
      <el-upload class="avatar"
                 action=""
                 :show-file-list="false"
                 :http-request="avatarUpload"
                 accept="image/jpeg"
                 name="new_avatar"

      >
        <el-avatar :src="$store.state.me.avatar" :size="100"
                   :key="$store.state.me.avatar">
        </el-avatar>
        <i class="el-icon-edit-outline avatar-uploader-icon"></i>
      </el-upload>
      <el-dialog
        width="750px"
        :visible.sync="avatarCropperDialog"
        :closeOnClickModal="false"
        center>
        <div class="avatar-cropper-box">
          <VueCropper
            ref="avatarCropper" class="avatar-cropper"
            :img="avatarImg" :outputSize="1"
            outputType="jpeg" :autoCrop="true"
            :fixed="true" :centerBox="true" mode="128px"
            @realTime="avatarRealTime"
          ></VueCropper>
          <div class="avatar-cropper-right">
            <div :style="avatarPreviewStyle" class="avatar-preview">
              <div :style="avatarPreview.div">
                <img :src="avatarImg" :style="avatarPreview.img">
              </div>
            </div>
            <el-button plain type="primary" class="avatar-cropper-submit" @click="avatarSubmit">修改头像</el-button>
          </div>
        </div>
      </el-dialog>
      <div class="header-right">
        <h2 class="name--big">{{ $store.state.me.name }}</h2>
        <div class="signature">
          <span v-if="!signature.edit">{{ $store.state.me.signature || '未设置个性签名' }}</span>
          <el-input v-if="signature.edit" type="text" placeholder="设置个性签名" v-model="signature.text"
                    maxlength="15" :clearable="true"
                    @change="setSignatureFinished" @blur="setSignatureFinished"
                    show-word-limit/>
          <el-button v-if="!signature.edit" icon="el-icon-edit"
                     @click="setSignature"
                     type="text"></el-button>
        </div>
      </div>
    </div>
    <el-descriptions
      title="基本信息" :column="2"
      :labelStyle="{width: '11%'}" :contentStyle="{width: '33%'}"
      border
    >
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-user"></i>
          用户名
        </template>
        {{ $store.state.me.name }}
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-key"></i>
          用户编号
        </template>
        {{ $store.state.me.uuid }}
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-ali-email"></i>
          邮箱
        </template>
        <span v-if="$store.state.me.email">
         {{ $store.state.me.email }}
        </span>
        <span v-if="!$store.state.me.email">
          <el-link type="primary" icon="el-icon-edit-outline">绑定邮箱</el-link>
        </span>
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-ali-mobile"></i>
          手机
        </template>
        <span v-if="$store.state.me.phone">
         {{ $store.state.me.phone }}
        </span>
        <span v-if="!$store.state.me.phone">
          <el-link type="primary" icon="el-icon-edit-outline" disabled>绑定手机</el-link>
        </span>
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-alarm-clock"></i>
          创建时间
        </template>
        <span>
         {{ $store.state.me.created | dateFormat }}
        </span>
      </el-descriptions-item>
    </el-descriptions>
  </div>
</template>

<script>

export default {
  name: 'SettingsViewPC',
  data () {
    return {
      avatarCropperDialog: false,
      avatarImg: '',
      avatarPreview: {},
      avatarPreviewStyle: {},
      signature: {
        edit: false,
        text: ''
      }

    }
  },
  methods: {
    setSignatureFinished () {
      this.signature.edit = false
      this.$axios.post('/api/v1/users/set_signature/', {'new_signature': this.signature.text})
        .then((resp) => {
          this.$store.commit('setSignature', this.signature.text)
        })
        .catch((err) => {
          this.$message({
            dangerouslyUseHTMLString: true,
            message: err.response.data,
            type: 'error'
          })
        })
    },
    setSignature () {
      this.signature.text = this.$store.state.me.signature
      this.signature.edit = true
    },
    avatarUpload (options) {
      this.avatarCropperDialog = true
      this.avatarImg = URL.createObjectURL(options.file)
    },
    avatarSubmit () {
      this.avatarLoading = true
      this.$refs.avatarCropper.getCropBlob(data => {
        let formData = new FormData()
        let file = new window.File([data], 'avatar.jpg', {type: 'image/jpeg'})
        formData.append('new_avatar', file)
        this.$axios
          .post(
            '/api/v1/users/set_avatar/', formData
          )
          .then(resp => {
            this.$nextTick(() => {
              this.$store.commit('setAvatar', this.BASEURL + resp.data.avatar)
            })
          })
          .catch((err) => {
            this.$message({
              dangerouslyUseHTMLString: true,
              message: err.response.data,
              type: 'error'
            })
          })
      })
      this.avatarCropperDialog = false
    },
    avatarRealTime (data) {
      const preview = data
      this.avatarPreviewStyle = {
        width: preview.w + 'px',
        height: preview.h + 'px',
        overflow: 'hidden',
        margin: '0',
        zoom: 100 / preview.w
      }

      this.avatarPreview = data
    }
  }
}
</script>

<style scoped>
.settings {
  width: 80%;
}

.avatar {
  border: 1px dashed transparent;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  width: 105px;
  height: 105px;
  display: inline-block;
}

.header {
  height: 120px;
  display: flex;
  flex-direction: row;
}

.header-right {
  margin-left: 30px;
  height: 100%;
  flex: 1;
  top: 0;
  display: flex;
  flex-direction: column;
}

.name--big {
  font-size: 30px;
  font-weight: normal;
}

.signature {
  font-size: 14px;
  color: gray;
  height: 16px;
  line-height: 16px;
  display: inline-block;
}

.avatar-cropper {
  height: 500px !important;
  width: 500px !important;
  display: inline-block;
  margin-right: 30px;
}

.avatar-cropper-box {
  margin: auto;
  width: 650px;
  text-align: center;
}

.avatar-cropper-right {
  display: inline-block;
  width: 100px;
  vertical-align: top;
}

.avatar-cropper-submit {
  margin-top: 30px;
}

.avatar-preview {
  border-radius: 50%;
  display: inline-block;
}

.avatar:hover {
  border: 1px dashed #409EFF;
}

.avatar:hover .avatar-uploader-icon {
  display: block;
}

.avatar-uploader-icon {
  font-size: 28px;
  color: #1d1e21;
  width: 100px;
  height: 100px;
  line-height: 100px;
  text-align: center;
  top: 0;
  left: 0;
  position: absolute;
  display: none;
  z-index: 999;
  background-color: rgba(255, 255, 255);
  opacity: 0.5;
}

</style>



